@import "../../../styles/helpers/_helpers";

.block-about {
	display: grid;
	grid-template-columns: repeat(var(--grid-column), 1fr);
	gap: 0 var(--grid-gap);
	.block__content {
		display: contents;
	}
	.block__imgs {
		display: grid;
		gap: 16px;
		grid-column: 2/span 7;
		grid-template-columns: 1fr 1fr;
	}
	.block__info {
		grid-column: 2/span 7;
	}
	.production-advantage {
		grid-column: 9/span 5;
	}
	.store-traffic {
		grid-column: 2/-2;
	}
	.btn {
		width: rem(250px);
	}
	.block__footer {
		margin-top: rem(40px);
	}
}
